<template>
  <div id="mapbox" v-on:keyup.122 = "fullClose" v-on:keyup.27 = "fullClose">
    <div id="viewDiv"></div>
    <!-- 地图的工具框 -->
    <ButtonGroup id="toolbar" vertical style="display: flex;flex-direction: column">
      <Tooltip content="距离测量" placement="right">
        <Button :type="type=='distance'?'primary':'default'" @click="clickToolbar('distance')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/length.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
      <Tooltip content="测量面积" placement="right">
        <Button :type="type=='area'?'primary':'default'" @click="clickToolbar('area')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/area.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
    </ButtonGroup>
  </div>
</template>

<script>
import arcgisPackage from '../../../utils/arcgisTools'
import config from '../../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie, shijie, xianjie } from '@/utils/layers'
import Basemapthumbnail from '../../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../../assets/map_images/Basemapthumbnail1.png'

export default {
  name: 'OnePiece',
  data () {
    return {
      map: null, // 地图实例
      view: null, // 地图视图实例
      type: '',
      addLayer: {}, // 增加的斑块图层
      subLayer: {} // 减少的斑块图层
    }
  },
  props: ['boxstate'],
  methods: {
    /**
     * @Description: 初始化“一张图”数据中心的地图
     * @author 张淼
     * @date 2019/4/16
    */
    async createMap () {
      // 导入需要的arcgis模块
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen
      let GraphicsLayer = await arcgisPackage.GraphicsLayer

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      this.map = new Map({
        basemap: customBasemap
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 7
      })
      this.addLayer = new GraphicsLayer({
        graphics: []
      })
      this.subLayer = new GraphicsLayer({
        graphics: []
      })
      this.map.add(this.addLayer)
      this.map.add(this.subLayer)
      // 添加工具框
      this.view.ui.add(document.getElementById('toolbar'), 'top-left')
      // 添加全屏
      this.fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.ui.add(this.fullscreen, 'top-left')
      // 增加地图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap1
      })
      this.view.ui.padding = { top: 20, left: 20, right: 55, bottom: 20 }
      this.view.ui.add(basemapToggle, 'top-right')
    },
    /**
     * @Description:测量工具点击事件
     * @author 张淼
     * @date 2019/4/11
     */
    async clickToolbar (type) {
      if (this.type === type) {
        type = null
      }
      this.type = type
      if (this.activeWidget) {
        this.view.ui.remove(this.activeWidget)
        this.activeWidget.destroy()
        this.activeWidget = null
      }
      switch (type) {
        case 'distance':
          let DistanceMeasurement2D = await arcgisPackage.DistanceMeasurement2D
          this.activeWidget = new DistanceMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        case 'area':
          let AreaMeasurement2D = await arcgisPackage.AreaMeasurement2D
          this.activeWidget = new AreaMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        default:
          break
      }
    },
    /* 渲染出人类活动 */
    async renderBankuai (addArr, subArr) {
      // 清除原来图层的元素
      this.addLayer.removeAll()
      this.subLayer.removeAll()
      let Graphic = await arcgisPackage.Graphic
      // 用于存放新增斑块的数组
      let graphicArr1 = []
      // 用于存放减少斑块的数组
      let graphicArr2 = []
      // 封装面积增加的graphic数组
      addArr.forEach(val => {
        val.type = 'polygon'
        val.symbol = {
          type: 'simple-fill',
          color: [ 219, 64, 82, 0.4 ],
          style: 'solid',
          outline: {
            color: 'white',
            width: 1
          }
        }
        graphicArr1.push(new Graphic(val))
      })
      // 添加增加面积的图层
      this.addLayer.addMany(graphicArr1)
      // 封装减少面积的graphic的数组
      subArr.forEach(val => {
        val.type = 'polygon'
        val.symbol = {
          type: 'simple-fill',
          color: [ 4, 170, 88, 0.4 ],
          style: 'solid',
          outline: {
            color: 'white',
            width: 1
          }
        }
        graphicArr2.push(new Graphic(val))
      })
      // 加载减少面积的图层
      this.subLayer.addMany(graphicArr2)
    },
    /* 定位到特定的斑块 */
    toPolygon (trend, index) {
    //  判断是减少的增加的图层
      let layer = trend ? this.addLayer : this.subLayer
      // 存储当前graphic的变量
      let currentGraphic = layer.graphics.items.filter(val => {
        return val.attributes.index === index
      })
      this.view.goTo(currentGraphic)
    }
  },
  async mounted () {
    // 初始化地图
    await this.createMap()
  }
}
</script>

<style lang="scss" scoped>
#mapbox{
  height: 100%;
  display: flex;
  position: relative;
}
#layersControl{
  .layersControl_box{
    padding:3px;
    background: #fff;
    border: 1px solid #d1d1d1;
    .fraggableBox{
      padding: 0;
      .fraggableLi{
        min-width: 100px;
        padding: 5px 8px;
        border-top: 1px solid #d1d1d1;
      }
    }
  }
}
#viewDiv{
  height: 100%;
  flex-grow: 2;
}
#maptab{
  width: 300px;
  .firstmenu{
    >li{
      margin-bottom: 6px;
      text-align: left;
      >ul{
        >li{
          padding-left: 20px;
        }
      }
      >p{
        line-height: 40px;
        text-align: left;
        font-size: 16px;
        background-color: rgb(42,177,245);
        color: white;
        padding-left: 10px;
        border-radius: 4px;
        cursor: pointer;
        i{
          font-size: 16px;
          line-height: 40px;
          margin-right: 10px;
        }
      }
      .secondmenu{
        label{
          line-height: 30px;
          font-size: 14px;
        }
      }
    }
  }
}
#basemapToggle{
  border: 5px #000 solid;
}
</style>
<style scoped>
#maptab >>> .ivu-tabs-content{
  height: calc(100% - 40px);
  margin-top: -16px;
}
#maptab >>> .ivu-tabs-content > .ivu-tabs-tabpane{
  padding: 16px;
  border: 1px solid #dcdee2;
  border-radius: 0 0 4px 4px;
  border-top: none;
}
#maptab >>> .ivu-tabs-nav{
  display: flex;
  float: none;
}
#maptab >>> .ivu-tabs-nav .ivu-tabs-tab{
  flex-grow: 2;
  margin-right: 0;
}
  .sliderBox >>> .ivu-slider-wrap{
    margin: 0 0 3px
  }
</style>
