  .query-box{
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    overflow: hidden;
    .search-title {
      float: left;
      font-size: 14px;
      &:before {
        content: '';
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url("../../../assets/redLine/sprite.png") 0 -63px no-repeat;
        background-size: auto;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
    .search-form {
      float: right;
    }
  }
  .content_box {
    padding: 0 15px 15px;
    width: 100%;
    height: calc(100% - 65px);
    #map {
      float: left;
      width: 40%;
      height: 100%;
      border: 1px solid #000;
    }
    .table-wrapper {
      float: right;
      width: calc(60% - 15px);
      height: 100%;
      p {
        margin-bottom: 5px;
        font-size: 16px;
        text-align: left;
      }
      &>div {
        margin-bottom: 15px;
        table {
          width: 100%;
          border: 1px solid #ccc;
          border-collapse: collapse;
        }
        thead th{
          text-align: center;
          background-color: #fafafa;
        }
        th,td {
          padding-left: 20px;
          border: 1px solid #e9eaec;
          line-height: 30px;
        }
      }
    }
    .page_box{
      width: 100%;
      padding: 15px 0 0;
      text-align: right
    }
  }
  .seeDetails {
    margin: 0 !important;
    height: calc(100% - 70px);
    width: calc(60% - 30px);
    position: absolute;
    right: -120%;
    top: 65px;
    z-index: 1356;
    background: #fff;
    transition: all 0.8s ease;
    .closeBtn{
      position: absolute;
      right:10px;
      bottom:10px;
      z-index: 131531313;
    }
  }
  .addActive{
    right: 15px;
  }
}
