












































































































































































































#mapbox{
  height: 100%;
  display: flex;
  position: relative;
}
#layersControl{
  .layersControl_box{
    padding:3px;
    background: #fff;
    border: 1px solid #d1d1d1;
    .fraggableBox{
      padding: 0;
      .fraggableLi{
        min-width: 100px;
        padding: 5px 8px;
        border-top: 1px solid #d1d1d1;
      }
    }
  }
}
#viewDiv{
  height: 100%;
  flex-grow: 2;
}
#maptab{
  width: 300px;
  .firstmenu{
    >li{
      margin-bottom: 6px;
      text-align: left;
      >ul{
        >li{
          padding-left: 20px;
        }
      }
      >p{
        line-height: 40px;
        text-align: left;
        font-size: 16px;
        background-color: rgb(42,177,245);
        color: white;
        padding-left: 10px;
        border-radius: 4px;
        cursor: pointer;
        i{
          font-size: 16px;
          line-height: 40px;
          margin-right: 10px;
        }
      }
      .secondmenu{
        label{
          line-height: 30px;
          font-size: 14px;
        }
      }
    }
  }
}
#basemapToggle{
  border: 5px #000 solid;
}
